import React from "react"
import Typist from "react-typist"

import Layout from "../components/Layout"
import { projects } from "../constants"

export default function Projects() {
  return (
    <Layout>
      <Typist
        cursor={{
          show: false,
        }}
      >
        <Typist.Delay ms={100} />
        <h1 className="projects-header">Featured Projects</h1>
      </Typist>
      <div className="projects-container fade-in">
        {projects.map(project => (
          <div className="card">
            <h2>{project.heading}</h2>
            <img src={project.img} alt="tictactoe" />
            <div className="content">
              <h4 align="center">{project.heading}</h4>
              <p>{project.description}</p>
              <div className="actions">
                <a rel="noreferrer" target="_blank" href={project.visit}>
                  Visit
                </a>
                <a rel="noreferrer" target="_blank" href={project.src}>
                  Source
                </a>
              </div>
            </div>
          </div>
        ))}
      </div>
    </Layout>
  )
}
